@-webkit-keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translate3d(-50%, 100%, 0);
  }

  to {
    opacity: 1;
    transform: translate(-50%, -50%);
  }
}
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translate(-50%, -50%) slate3d(-50%, 100%, 0);
  }

  to {
    opacity: 1;
    transform: translate(-50%, -50%);
  }
}

.giftIcon {
  height: 48px;
  width: 48px;
}

.cart-item {
  margin-left: 8px;
  background-color: #000;
  width: 24px;
  height: 24px;
  transform: translate(0px, 0px) rotate(-30deg) scale(0.6);
  border-radius: 3px;
  top: -55px;
  left: 12px;
  z-index: -1;
  opacity: 0;
}

.giftIconAdded {
  -webkit-animation: item 1s linear;
          animation: item 1s linear;
}

@-webkit-keyframes item {
  0% {
    opacity: 0;
    transform: translate(0px, 0px) rotate(-40deg) scale(1.3);
  }
  20% {
    opacity: 1;
  }
  35% {
    transform: translate(0px, 15px) rotate(0deg) scale(0.6);
    opacity: 0.5;
  }
  60% {
    transform: translate(0px, 55px) rotate(0deg) scale(0.6);
    opacity: 0.25;
  }
  100% {
    transform: translate(0px, 55px) rotate(0deg) scale(0.6);
    opacity: 0;
  }
}

@keyframes item {
  0% {
    opacity: 0;
    transform: translate(0px, 0px) rotate(-40deg) scale(1.3);
  }
  20% {
    opacity: 1;
  }
  35% {
    transform: translate(0px, 15px) rotate(0deg) scale(0.6);
    opacity: 0.5;
  }
  60% {
    transform: translate(0px, 55px) rotate(0deg) scale(0.6);
    opacity: 0.25;
  }
  100% {
    transform: translate(0px, 55px) rotate(0deg) scale(0.6);
    opacity: 0;
  }
}

.shake {
  -webkit-animation: cartShake 0.7s;
  -webkit-animation-delay: 300ms;
}

@-webkit-keyframes cartShake {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(0deg);
  }
  70% {
    transform: rotate(-10deg);
  }
  80% {
    transform: rotate(10deg);
  }
  90% {
    transform: rotate(-10deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

.giftIcon:after {
  content: "";
  display: block;
  position: absolute;
  top: 100%;
  right: 0;
  bottom: 0;
  left: 0;
  background: #333333;
  z-index: -1;
  transition: all 0.1s cubic-bezier(0, 0.59, 1, 0.26);
}

.giftIcon:after {
